@import url(https://fonts.googleapis.com/css?family=Overpass:600|Source+Sans+Pro:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center; }

.container {
  width: 1000px; }

hr {
  margin: 112px 0 95px 0; }

.font-families__title {
  color: #393939;
  font-family: "Open Sans", sans-serif;
  font-size: 34px;
  font-weight: 400;
  margin-bottom: 45px; }

.font-families__font {
  margin-top: 129px;
  display: flex;
  justify-content: space-between; }
  .font-families__font:nth-child(2) {
    margin-top: 0; }

.font-families__font-properties {
  margin-top: 16px; }

.font-families__font-property {
  display: grid;
  grid-template-columns: 100px 150px;
  color: #626262;
  font-family: "Open Sans";
  font-size: 14px;
  margin-top: 16px; }
  .font-families__font-property:first-child {
    margin-top: 0; }

.font-families__property-value {
  margin-top: 16px; }
  .font-families__property-value:first-child {
    margin-top: 0; }

.font-families__example {
  display: flex;
  align-items: center;
  height: 260px;
  margin-left: 6px;
  border: 1px solid #d0d0d0;
  flex-grow: 1;
  padding-bottom: 10px;
  box-sizing: border-box; }
  .font-families__example.overpass {
    font-family: "Overpass";
    font-weight: 600; }
  .font-families__example.source-sans-pro {
    font-family: "Source Sans Pro";
    font-weight: 400; }

.font-families__example-literal {
  display: flex;
  align-items: center;
  color: #222222;
  font-size: 120px;
  width: 265px;
  padding-left: 75px;
  box-sizing: border-box; }

.font-families__example-word {
  color: #222222;
  font-size: 60px;
  padding-top: 10px; }

